<template>
<div class="login">
	<h1>拉扯网后台管理系统</h1>
	<div class="container">
		<input type="text" name="username" v-model.trim="username" class="form-control" placeholder="name" />
		<input type="password" name="password" v-model.trim="password" class="form-control" placeholder="password" @keyup.enter="doLogin" />
		<button @click="login" class="btn btn-primary">登录</button>
	</div>
</div>
</template>
<script>
import { Toast, Indicator } from 'mint-ui'
import { mapState } from 'vuex'
export default {
	name: 'login',
	components: {},
	data() {
		return {
			username: '',  
			password: '',
			lachewang: {}
		}
	},
	created() {},
	mounted() {
		this.lachewang = JSON.parse(localStorage.lachewang)
		if(this.lachewang.admin.isLogin) {
			this.$router.push('/admin')
		}
	},
	methods: {
		doLogin() {
			this.login()
		},
		login() {
			let isLogin = this.lachewang.admin.isLogin
			let user = this.lachewang.admin.user
			let currentUser = {}

			if(isLogin){
				this.$router.push('/admin');
				return false;
			}
			
			if(!this.username){ Toast({message: '请输入账号', duration: 1500}); return false; }
			if(!this.password){ Toast({message: '请输入密码', duration: 1500}); return false; }
			
			user.forEach(item => {
				if(item.type === 'administrator') {
					currentUser = item
				}
			})
			if(this.username === currentUser.name) {
				if(this.username === currentUser.name && this.password === currentUser.password) {	//在数据库login表里设置登录状态
	  				this.setLoginStatus();
	  				//弹出登录成功提示
	  				let instance = Toast({
	  					message: '登录成功', 
	  					iconClass: 'iconfont icon-success1',
	  					duration: 1500
	  				})
	  				//提示窗消失后跳转到管理后台
	  				setTimeout(() => {
					  	instance.close();
					  	this.username = ''
						this.password = ''
					  	this.$router.push('/admin');
					}, 2000);
				//如果账号或密码校验失败
	  			} else {			
	  				//分别弹出提示
	  				if(this.username !== currentUser.name) {
	  					Toast({message: '账号错误', duration: 1500})
	  				} else  if(this.password !== currentUser.password) {
	  					Toast({message: '密码错误', duration: 1500})
	  				}
				}
			} else {  			
	  			//目前就一个管理账号登录，因此这里直接提示验证失败
	  			Toast({message: '账号验证失败', duration: 1500});
	  		}	
		},
		//写入登录状态信息
		setLoginStatus () {
			this.lachewang.admin.isLogin = true
			localStorage.lachewang = JSON.stringify(this.lachewang)
		}
	},
	watch: {},
	computed: {
		...mapState(['server'])
	}
}
</script>

<style scoped lang="less">
.login{width:100%;height: 100%;display: flex;align-items: center;flex-direction: column;padding-top: 100px;box-sizing: border-box;
	h1{color: #373277;margin-bottom: 100px;}
	.container{display: flex;justify-content: center;align-items: center;flex-direction: column;
		input{margin-bottom: 20px;}
		.btn{background-color: #373277;color:#fff;border-color: #373277;}
		input, button{width:300px;}
	}
}
</style>


