<template>
<div class="articleIndex">
	<ul class="list">
		<li v-for="(l,index) in list" :key="l.title">
			<h4><a :href="l.url" target="_blank">{{l.title}}</a></h4>
			<p v-if="l.desc!=''">{{l.desc}}</p>
			<div class="tags">
				<a v-for="t in l.tags" :href="t.url">{{t.name}}</a>
			</div>
			<div class="source">
				<button class="zan" :class="l.praised" @click="zan(index)">
					<i class="icon iconfont icon-zan1"></i>
					<span class="num" v-if="l.zan>0">x {{l.zan}}</span>
					<span class="text">赞</span>
				</button>
				<a :href="l.source.url">{{l.source.name}}</a>
				<time :datetime="l.time" pubdate>{{l.time}}</time>
			</div>
		</li>
	</ul>
</div>
</template>

<script>
import axios from 'axios'
export default {
	name: 'articleIndex',
	components: {},
	data () {
		return {
			msg: 'articleIndex',
			list: []
		}
	},
	activated () {
	    document.title="文章列表"
	},
	created(){
		this.getData()
	},
	methods: {
		zan(i){
			this.list[i].praised==='on' ? this.list[i].praised='' : this.list[i].praised='on';
			this.list[i].praised==='on' ? this.list[i].zan++ : this.list[i].zan--;
		},
		getData () {
	      	axios.get('/static/db/article.json').then(res => {
	        	this.list = res.data;
	      	}).catch(err => {
	        	console.log(err);
	      	})
	    }
	}
}
</script>

<style scoped lang="less">
ul,li{padding:0;margin:0;list-style: none;}
.articleIndex{
	.list{
		li{width:100%;padding:20px 0;
			h4{font-weight: 500;font-size: 18px;line-height: 28px;
				a{color: #212121;}
			}
			p{color: #888;line-height: 1.5;font-size: 13px;font-family: -apple-system,BlinkMacSystemFont,Helvetica Neue,PingFang SC,Microsoft YaHei,Source Han Sans SC,Noto Sans CJK SC,WenQuanYi Micro Hei,sans-serif;overflow: hidden;text-overflow: ellipsis;display: -webkit-box;-webkit-line-clamp: 2;-webkit-box-orient: vertical;}
			.tags{height: 30px;overflow: hidden;font-size: 12px;display: inline-flex;align-items: center;flex-wrap: wrap;width: 100%;margin-bottom: 10px;
				a{background-color: #f1f8ff;border-radius: 3px;display: inline-block;margin: 0 .5em .5em 0;padding: .3em .9em;white-space: nowrap;}
			}
			.source{font-size: 13px;
				.zan{padding:0;margin:0 10px 0 0;border:none;background-color:transparent;box-shadow:none;border:none;outline:none;transition:all 0.3s;
					.icon{background-color: rgba(1,126,102,0.08);color: #009a61;width:24px;height: 24px;border-radius: 100%;display: inline-block;font-style: normal;transition:all 0.3s;}
					.num{color: #009a61;transition:all 0.3s;padding: 0 5px;}
					.text{color:#666;transition:all 0.3s;}
					&:hover{
						cursor:pointer;
						.icon{background-color:#009a61;
							&:before{color:#fff;}
						}
						.text{color: #009a61;}
					}
					&.on{
						cursor:pointer;
						.icon{background-color:#009a61;
							&:before{color:#fff;}
						}
						.text{
							&:before{content:"已";}
						}	
					}
					&:after{content:"";box-shadow: none;border:none;background-color: transparent;}
				}
				a{color:#586069;margin: 0 10px 0 0;}
				time{color: #9E9E9E;}
			}
		}
	}
}
@media screen and(max-width:640px){
	.articleIndex{padding:0 10px;}
} 
</style>


