<template>
<div class="catalog">
	<header>
		<button class="btn btn-primary btn-sm" @click="openAddCatalogDialog">+添加栏目</button>
	</header>
	<ul class="list-group" v-if="catalogs && catalogs.length>0">
		<adminCatalogItem
			v-for="catalog in columns"
			:key="catalog.id"
			:class="catalog.status?'':'disable'"
			:catalog="catalog"
		>
		</adminCatalogItem>
	</ul>
	<AdminAddCatalog :catalogList="columns"></AdminAddCatalog>
</div>
</template>
<script>
import AdminCatalogItem from './AdminCatalogItem.vue'
import AdminAddCatalog from './AdminAddCatalog.vue'
export default {
	name: 'AdminCatalog',
	components: { AdminCatalogItem, AdminAddCatalog },
	data () {
		return {
			lachewang: {},
			catalogs: {},
			columns: []
		}
	},
	activated () {
		this.init()
	},
	mounted () {
		let that = this
		this.BUS.$on('updateCatalog', () => {
			that.init()
		})
	},
	methods: {
		init() {
			this.lachewang = JSON.parse(localStorage.lachewang)
			this.catalogs = this.lachewang.admin.catalogs
			this.columns = []
			//循环总栏目列表
			if(this.catalogs.length>0) {
				let cs = this.catalogs;
				let arr = [];
				//将栏目按一级栏目、二级栏目、三级栏目这样组合，放到一个数组中，组成一个二维数组
				cs.forEach(b => {
					let l = b.level
					let i = l-1
					if(!arr[i] || arr[i].length==0) {
						arr[i] = []
					}
					let j = arr[i].length
					arr[i][j]=b
				})
				//如果有二级栏目，则开始递归循环，否则就不用白费劲了
				if(arr[1] && arr[1].length>0) {
					//循环二维数组
					arr.forEach((item,index) => {
						let i = index+1
						if(arr[i] && arr[i].length>0) {
							//每次用下级栏目循环上级父栏目
							this.digui(item, arr[i])
						}
					})
				} 
				//注意，arr数组只是一个组装数据的工具，最后全部归纳到了arr二维数组的第一个子数组中，所以这里要用arr[0]表示完整数据
				this.columns = arr[0]
			}
		},
		digui(a0,aNext) {
			aNext.forEach((an,index) => {
				//通过try catch方式，在循环到正确位置时，完成相关操作后，通过throw终止循环，继续下一轮，以此来减少遍历次数
				try {
					a0.forEach((i,index) => {
						//如果下级栏目的父ID是上级栏目的ID
						if(an.parentID == i.id) {
							//则上级栏目把下级栏目收入children中
							i.children.push(an)
							//return false;
							throw new Info('exist')
						}
					})
				} catch (e) {
					if(e.message=='exist') throw e
				}
			})
		},
		//控制栏目管理弹窗显示或隐藏
		openAddCatalogDialog () {
			let lachewang = JSON.parse(localStorage.lachewang)
			lachewang.admin.addCatalogDialog = true
			localStorage.lachewang = JSON.stringify(lachewang)
			this.BUS.$emit('openAddCatalogDialog', true)
		}
	},
	watch: {},
	computed: {}
}
</script>
<style scoped lang="less">
.catalog{
	header{margin-bottom: 1vw;}
}
</style>


