<template>
	<div class="add">
		<input type="text" name="title" class="form-control mb20" placeholder="title" />
		<input type="text" name="subTitle" class="form-control mb20" placeholder="subTitle" />
		<div class="catalog mb20">
			<input type="text" name="catalog" class="form-control" placeholder="get focus to choose catalog" />
		</div>
		<div class="tags mb20 pr">
			<input type="text" class="form-control" placeholder="press enter to split tags, max 5" @keyup.enter="jionTagList" v-model.trim="tag.value" @keypress="maxTags" />
			<ul class="tagGroups" ref="tagGroups">
				<li v-for="(t, index) in tag.list" :key="index">
					<span>{{t}}</span>
					<button class="btn" @click="delTag(t)"><i class="iconfont icon-guanbi11"></i></button>
				</li>
			</ul>
		</div>
		<div class="articleEnable mb20">
			<span>{{articleEnable ? 'enable' : 'disabled'}}</span>
			<mt-switch v-model="articleEnable"></mt-switch>
		</div>
		<admin-article-ueditor></admin-article-ueditor>
		<br/>
		<button class="btn btn-success btn-block btn-lg">Save</button>	
	</div>
</template>
<script>
import Vue from 'vue'
import { Toast, Switch } from 'mint-ui'
import AdminArticleUeditor from './Ueditor'
Vue.component(Switch.name, Switch);
export default {
	name: 'Admin_Article_Add',
	components: { AdminArticleUeditor },
	data () {
		return {
			articleEnable: true,
			tag: {
				speed: 5,
				value: '',
				list: []
			}
		}
	},
	activated() {
		let id = this.$route.params.id
		if(id=='') {
			this.$router.push('/admin/article')
		} else if(id!=0 && id.toString().length<13){
			this.$router.push('/admin/article/add/0')
		}
	},
	created() {},
	mounted() {
		this.setTagsTextIndent()
	},
	methods: {
		//根据标签数量及宽度，调整输入标签的样式
		setTagsTextIndent() {
			setTimeout(() => {
				let inputNode = this.$refs.tagGroups.parentNode.children[0]
				let tagGroupsWidth = this.$refs.tagGroups.offsetWidth
				let pLeft = parseFloat(tagGroupsWidth/10).toFixed(2)
				if(this.tag.list.length==0) {
					inputNode.style.paddingLeft = '0.75rem'
				} else {
					inputNode.style.paddingLeft = tagGroupsWidth+5+'px'
				}
			},200)
		},
		//删除当前标签
		delTag(t) {
			if(this.tag.list.length==1) {
				this.tag.list = []
			} else if(this.tag.list.length>1) {
				this.tag.list = this.tag.list.filter(i => i!=t)
			}
			this.setTagsTextIndent()
		},
		//加入tag到列表
		jionTagList() {
			if(this.tag.value) {
				this.tag.list.push(this.tag.value)
				this.tag.value = ''
				this.setTagsTextIndent()
			}
		},
		//maxTags
		maxTags() {
			if(this.tag.list.length >= this.tag.speed) {
				this.tag.value = ''
				Toast({message: '最多只能添加5个标签', duration: 1500});
			}
		}
	},
	watch: {},
	computed: {}
}
</script>
<style scoped lang="less">
.add{
	.mb20{margin-bottom: 20px;}
	.pr{position: relative;}
	.catalog{}
	.tags{
		input{transition: all 0.3s;}
		.tagGroups{position:absolute;left:0;top:0;z-index:2;display: flex;width:auto;height: 100%;padding:5px 0;box-sizing: border-box;
			li{display: flex;align-items: center;height:100%;margin:0 0 0 5px;background-color: #e7f2ed;
				span{color: #017e66;padding-left:5px;height:100%;display: flex;align-items: center;}
				button{padding:2px;height:100%;display: flex;align-items: center;
					i{color: #017e66;font-size: 14px;height: 17px;}
				}
				button,button:hover,button:active{outline: none;box-shadow: none;}
			}
		}
	}
	.articleEnable{display: flex;align-items: center;
		.mint-switch{margin-bottom: 0;margin-left: 20px;}
		.mint-switch:hover{cursor: pointer;}
	}
}
</style>	


