<template>
<div class="system">
	<header>
		<button class="btn btn-success">Save</button>
	</header>
	<section>
		<div class="item"><span>name</span><input type="text" class="form-control" v-model="name"></div>
		<div class="item"><span>host</span><input type="text" class="form-control" v-model="host"></div>
		<div class="item"><span>keywords</span><input type="text" class="form-control" v-model="keys"></div>
		<div class="item"><span>description</span><input type="text" class="form-control" v-model="desc"></div>
		<div class="item"><span>备案号</span><input type="text" class="form-control" v-model="beianID"></div>
		<div class="item"><span>联系人</span><input type="text" class="form-control" v-model="contact"></div>
		<div class="item"><span>手机号</span><input type="text" class="form-control" v-model="tel"></div>
		<div class="item"><span>微信号</span><input type="text" class="form-control" v-model="wechat"></div>
		<div class="item"><span>QQ号</span><input type="text" class="form-control" v-model="qq"></div>
		<div class="item"><span>Email</span><input type="text" class="form-control" v-model="email"></div>
		<div class="item"><span>统计代码</span><input type="text" class="form-control" v-model="tongji"></div>
		<div class="item"><span>版权信息</span><input type="text" class="form-control" v-model="copyRight"></div>
		<div class="item">
			<span>{{switchSite?'enable':'disabled'}}</span>
			<mt-switch v-model="switchSite" class="form-control" style="border:none;"></mt-switch>
		</div>
		<div class="item" v-if="!switchSite">
			<span>关闭原因</span><input type="text" class="form-control" v-model="closeReason" />
		</div>
	</section>
</div>
</template>
<script>
import Vue from 'vue'
import { Switch } from 'mint-ui';
Vue.component(Switch.name, Switch);
export default {
	name: 'Admin_System',
	components: {},
	data () {
		return {
			name: '拉扯网',
			host: 'vlon.cn',
			keys: '拉扯网',
			desc: '拉扯网是刘威龙的个人网站',
			beianID: '豫ICP备2026444',
			contact: 'vlon.cn',
			tel: '',
			wechat: 'eddyscofield',
			qq: '894492015',
			email: '894492015@qq.com',
			tongji: '',
			copyRight: '©2019 THE VLON.CN COPYRIGHT',
			switchSite: true,
			closeReason: '由于维护原因，网站暂时关闭，由此带来的不便请亲原谅，大概会在10分钟后开放网站，敬请期待！'
		}
	},
	created (){},
	activated() {
		this.BUS.$emit('setSideActived', this.$route.name)
	},
	mounted () {},
	methods: {},
	watch: {},
	computed: {}
}
</script>
<style scoped lang="less">
.system{
	header{width:100%;display: flex;justify-content: center;margin-bottom: 1%;}
	.item{width:100%;display: flex;margin-bottom: 20px;align-items: center;
		&>span{width:10%;text-align: right;padding-right: 2%;font-size: .8rem;}
	}
}
</style>


