<template>
  <div class="front" :class="this.front.showMenu">
    <mt-popup 
      v-model="popupVisible" 
      position="top" 
      :modal="false" 
      :closeOnClickModal="false"
      >系统升级维护, 当前为静态资源浏览模式
      <button @click="closePopup" class="btn btn-default">
        <i class="iconfont icon-quxiaodingdan"></i>
      </button>
    </mt-popup>
    <front-top></front-top> 
    <div class="container">
        <div class="row">
          <keep-alive>
            <router-view/>
          </keep-alive>
        </div>
    </div>
    <front-foot></front-foot> 
  </div>
</template>
<script>
  import Vue from 'vue'
import { Popup } from 'mint-ui'
import FrontTop from './top/Index'
import FrontFoot from './foot/Index'
import { mapState } from 'vuex'
Vue.component(Popup.name, Popup);
export default {
  name: 'Front',
  data () {
    return {
      popupVisible: false
    }
  },
  components: { FrontTop, FrontFoot },
  mounted () {
    document.title = "首页";
    setTimeout(()=>{
      this.popupVisible = true
    },2000)
  },
  computed: {
     ...mapState(['front'])
  },
  methods: {
    closePopup() {
      this.popupVisible = false
    }
  }
}
</script>
<style scoped lang="less">
.front{font-family: 'Avenir', Helvetica, Arial, sans-serif;-webkit-font-smoothing: antialiased;-moz-osx-font-smoothing: grayscale;width:100%;height: 100%;transition: all 0.3s;
  .container{width:100%;}
  .row{height: 100%;}
  &.showMenu{margin-left:-50%;}
  .mint-popup{width:100%;height:60px;line-height:60px;background-color: rgba(0,0,0,0.5);color:#fff;text-align: center;display: flex;align-items: center;justify-content: center;
    button{position: absolute;right:20px;color:#fff;}
  }
}
</style>


