<template>
<div class="user card">
	<div class="card-header">
		<span>管理人员</span>
		<div>
			<router-link to="/admin/user/addManager" class="btn btn-success">Add Manager</router-link>
			<router-link to="/admin/user/groups" class="btn btn-primary">Add/Edit Groups</router-link>
		</div>
	</div>
	<div class="card-body">
		<ul>
			<li class="header">
				<span>ID</span>
				<span>账号</span>
				<span>角色</span>
				<span>状态</span>
				<span>创建时间</span>
				<span>操作</span>
			</li>
			<li v-for="l in list" :key="l.id">
				<span>{{l.id}}</span>
				<span>{{l.name}}</span>
				<span>{{l.groups}}</span>
				<span><mt-switch v-model="l.switchAccount"></mt-switch></span>
				<span>{{l.createTime}}</span>
				<span><button class="btn" @click="update(l.id)"><i class="iconfont icon-pingjia"></i></button></span>
			</li>
		</ul>
	</div>	
	<!-- <footer class="card-footer"></footer> -->
</div>
</template>
<script>
import Vue from 'vue'
import { Switch } from 'mint-ui';
Vue.component(Switch.name, Switch);
export default {
	name: 'Admin_User',
	components: {},
	data () {
		return {
			switchAccount: true,
			list: [
				{
					id: 1,
					name: 'admin',
					groups: 'administrator',
					switchAccount: true,
					createTime: '2019-08-20 17:54:20'
				},{
					id: 2,
					name: 'vlon',
					groups: 'article',
					switchAccount: true,
					createTime: '2019-08-20 17:54:20'
				},{
					id: 3,
					name: 'eddy',
					groups: 'catalog',
					switchAccount: true,
					createTime: '2019-08-20 17:54:20'
				}
			]
		}
	},
	created (){},
	activated() {
		this.BUS.$emit('setSideActived', this.$route.name)
	},
	mounted () {},
	methods: {
		update(id) {
			if(id) {
				this.$router.push('/admin/user/update/'+id)
			} else {
				Toast({message: '未获得ID', duration: 1500});
			}
		}
	},
	watch: {},
	computed: {}
}
</script>
<style scoped lang="less">
.user{
	.card-header{width:100%;display: flex;justify-content:space-between;}
	.card-body{
		li{width:100%;display: flex;justify-content: space-around;font-size: 1rem;padding:10px 0;text-align: center;color:#999;
			span{flex:1;text-align: center;}
			.mint-switch{justify-content:center;}
		}
		.header{font-size: 1rem;color:#000;font-weight: 700;}
	}
}
</style>


