<template>
<div class="recycle card">
	<header class="card-header">
		<button class="btn btn-success btn-sm" @click="removeRecycle">移出回收站</button>
		<button class="btn btn-danger btn-sm" @click="batch('del')">批量删除</button>
	</header>
	<ul class="list card-body" ref="list">
		<li v-for="l in currentPages()" :key="l.id">
			<div class="con">
				<button class="btn btn-default choose" @click="chooseThis($event, l)">
					<i class="iconfont icon-fangxingweixuanzhong"></i>
				</button>
				<button class="btn title" :class="l.enable?'':'disabled'" @click="updateArticle(l.id)">{{l.name}}</button>
			</div>
			<div class="set">
				<button class="btn" title="删除" @click="deleteArticle(l.id)">
					<i class="iconfont icon-shanchu1"></i>
				</button>
			</div>
		</li>
	</ul>
	<footer v-if="pages.count>0" class="card-footer">
		<button class="btn btn-default" @click.stop.prevent='toFirst' :class="pages.current>1 ? '':'disabled'">首页</button>
		<button class="btn btn-default" @click.stop.prevent='toPrev' :class="pages.current>1 ? '':'disabled'">上一页</button>
		<button class="btn" 
			v-for="p in getPages()" 
			:key="p" 
			:class="p===pages.current ? 'btn-primary' : 'btn-default'"
			@click.stop.prevent='toThisPage(p)'
		>{{p}}</button>
		<button class="btn btn-default" @click.stop.prevent='toNext' :class="pages.current<pages.column ? '':'disabled'">下一页</button>
		<button class="btn btn-default" @click.stop.prevent='toLast' :class="pages.current<pages.column ? '':'disabled'">末页</button>
	</footer>
</div>
</template>
<script>
import { mapState } from 'vuex'
import { Toast, MessageBox } from 'mint-ui'
export default {
	name: 'Admin_Recycle',
	components: {},
	data () {
		return {
			list: [],
			lachewang: {},
			pages: {
				//当前页码
				current: 1,
				//总列表数
				count: 0,
				//每页列表数
				size: 14,
				//总页数
				column: 0,
			},
			ids: []
		}
	},
	created (){},
	activated() {
		this.initList()
		this.initPages()
		this.BUS.$emit('setSideActived', this.$route.name)
	},
	mounted () {
		setTimeout(()=>{
			this.setListHeight()
		}, 500)
	},
	methods: {
		initList() {
			this.lachewang = JSON.parse(localStorage.lachewang)
			this.list = this.lachewang.admin.articles
		},
		initPages() {
			this.pages.count = this.list.length;
			if(this.pages.count > 0) {
				if(this.pages.count%15 == 0) {
					this.pages.column = this.pages.count/15
				} else {
					this.pages.column = Math.floor(this.pages.count/15) + 1
				}
			} else {
				this.pages.current = 1
				this.pages.count = 0
				this.pages.size = 15
				this.pages.column = 0
			}
		},
		//切换选中或取消选择
		chooseThis(thisDom, obj) {
			this.switchStyle(thisDom, {
				t: 'choose',
				y: 'icon-fangxingxuanzhongfill',
				n: 'icon-fangxingweixuanzhong',
				obj
			})
		},
		switchStyle(thisDom,obj) {
			let iDom, cn;
			if(thisDom.target.nodeName === 'I'){
				iDom = thisDom.target
			}else if(thisDom.target.nodeName === 'BUTTON') {
				iDom = thisDom.target.children[0]
			}
			cn = iDom.className
			if(cn.indexOf(obj.n)!=-1){
				iDom.className = 'iconfont '+obj.y
				if(obj.t === 'choose') {
					iDom.parentNode.parentNode.parentNode.className = 'active'
					//当选中时，收集当前ID
					if(this.ids.length==0) {
						this.ids.push(obj.obj.id)
					}else if(this.ids.length>0) {
						let idss = this.ids.join()
						if(idss.indexOf(obj.obj.id)==-1){
							this.ids.push(obj.obj.id)
						}
					}
				} else if(obj.t === 'show') {
					this.setArticleDisabled(obj.obj.id, false)
				}
			} else {
				iDom.className = 'iconfont '+obj.n
				if(obj.t === 'choose') {
					iDom.parentNode.parentNode.parentNode.className = ''
					//当取消选择时删掉指定ID
					if(this.ids.length==1 && this.ids[0] === obj.obj.id) {
						this.ids = []
					}else if(this.ids.length>1) {
						let idss = this.ids.join()
						if(idss.indexOf(obj.obj.id)!=-1){
							this.ids = this.ids.filter(i => i != obj.obj.id)
						}
					}
				} else if(obj.t === 'show') {
					this.setArticleDisabled(obj.obj.id, true)
				}
			}
		},
		setArticleDisabled(id,t){
			this.list.forEach(i => {
				if(i.id===id) i.enable = t
			})
		},
		toFirst() {
			this.pages.current = 1
			this.currentPages()
		},
		toPrev() {
			if(this.pages.current>1){
				this.pages.current = this.pages.current-1
				this.currentPages()
			}
		},
		toNext() {
			if(this.pages.current<this.pages.column){
				this.pages.current = this.pages.current+1
				this.currentPages()
			}
		},
		toLast() {
			this.pages.current = this.pages.column
			this.currentPages()
		},
		toThisPage(p){
			if(p!==this.pages.current){
				this.pages.current = p
				this.currentPages()
			}
		},
		//分页显示列表
		currentPages() {
			if(this.list.length>0) {
				let arr = this.list
				let p = this.pages
				let con = []
				let start = 0
				let scope = p.current*p.size
				if(p.current>1 && p.current<p.column){
					start = (p.current-1)*p.size
					scope = p.current*p.size
				}else if(p.current==p.column){
					start = (p.current-1)*p.size
					scope = arr.length
				}
				for(let i=start; i<scope; i++) {
					con.push(arr[i])
				}
				return con
			} else {
				return []
			}
		},
		//显示页码
		getPages() {
			let arr = []
			let p = this.pages
			if(p.column>0) {
				for(let i=1; i<=p.column; i++) {
					arr.push(i)
				}
			}
			return arr
		},
		//设置列表框架的高
		setListHeight() {
			let list = this.$refs.list
			let lis = list.children
			if(lis.length>0) {
				let li = lis[0]
				let h = li.offsetHeight || li.clientHeight || li.scrollHeight
				list.style.minHeight = h * this.pages.size + 'px'
			}
			console.log('setListHeight')
		},
		//删除指定文章
		deleteArticle(id) {
			if(id) {
				MessageBox.confirm('确定删除?').then(action => {
					if(action === 'confirm') {
						if(this.list.length==1) {
							let l = this.list[0]
							if(l.id === id) { 
								this.list = []
							} else {
								console.log('剩最后一条，还和当前执行的不是同一个ID，见了鬼了')
							}
						} else if(this.list.length > 1) {
							this.list = this.list.filter(i => i.id != id)
						}
						this.deleteSuccess()
						this.updatePages()
					} else {
						console.log(action)
					}
				}).catch(err => {
					if(err === 'cancel') {
						console.log('取消删除')
					} else {
						console.log(err)
					}
				})
			} else {
				console.log('未知id')
			}
		},
		//批量操作
		batch(t) {
			if(this.ids.length>0) {
				switch(t) {
					case 'del': this.batchDelete(); break;
				}
			} else {
				Toast({message: '请选择', duration: 1500});
			}
		},
		//批量删除
		batchDelete() {
			MessageBox.confirm('确定删除?').then(action => {
				if(action === 'confirm') {
					let arr = []
					let idss = this.ids.join()
					if(this.list.length>1){
						this.list.forEach(l => {
							if(idss.indexOf(l.id) == -1) {
								arr.push(l)
							}
						})
						this.list = arr
					} else if(this.list.length===1) {
						let l = this.list[0]
						if(l.id === idss) {
							this.list = []
						}
					}
					this.deleteSuccess()
					this.updatePages()
				} else {
					console.log(action)
				}
			}).catch(err => {
				if(err === 'cancel') {
					console.log('取消删除')
				} else {
					console.log(err)
				}
			})
		},
		//重新排列底部页码，及当前页码所在位置，以及重新显示当前页码内的文章列表
		updatePages() {
			this.initList()
			//删除成功之后，根据剩余文章列表数，重新排页码
			this.initPages()
			//若删除操作时，当前页码数大于重新排列的总页码数，说明删除之后，页码数减少了，需将当前页码数往前移到最后一页；若不大于重新排列的总页码数，则保持不变
			this.pages.column>0 ? this.pages.current>this.pages.column ? this.pages.current=this.pages.column : this.pages.current=this.pages.current : this.pages.current=1
			//根据当前页码，重新请求当前页码内的文章列表
			this.currentPages()
			//显示底部重新排好的页码
			this.getPages()
		},
		//删除成功
		deleteSuccess() {
			this.ids = []
			Toast({
			  	message: '删除成功',
			  	iconClass: 'iconfont icon-success1',
			  	duration: 1200
			})
			localStorage.lachewang = JSON.stringify(this.lachewang)
		},
		//移出回收站
		removeRecycle() {
			if(this.ids.length>0) {
				
			} else {
				Toast({message: '请选择', duration: 1500});
			}	
		}
	},
	watch: {},
	computed: {
		...mapState(['server'])
	}
}
</script>
<style scoped lang="less">
.recycle{
	header{width:100%;display: flex;align-items: center;
		button{margin-right: 10px;}
	}
	.icon-fangxingweixuanzhong,.icon-fangxingxuanzhongfill{font-size: 20px;position: relative;top:3px;}
	.yan i{color:#666;}
	.icon-fangxingxuanzhongfill{color:#007bff;}
	.icon-shanchu1,.icon-fangxingweixuanzhong{color:#999;}
	.list{padding-top:10px;
		li{width:100%;display: flex;justify-content: space-between;align-items: center;border-bottom: 1px solid #ddd;transition: all 0.3s;
			&:last-child{border-bottom:0;}
			.con{flex:1;display: flex;}
			button{transition: all 0.3s;}
			button,button:active,button:hover{outline: none;box-shadow: none;}
			.title{flex-wrap: wrap;text-align: left;flex:1;color:#000;transition: all 0.3s;font-size: 14px;}
			.title.disabled{color:#999;}
			&:hover,&.active{background-color: #f5f5f5;
				.title{color:#4fc08d;}
			}
		}
	}
	footer{width:100%;display: flex;justify-content: flex-end;align-items: center;padding-top:20px;
		button{border:1px solid #ddd;margin-right:10px;font-size: 14px;}
	}
}
</style>


