<template>
<div class="index">
	<admin-catalog></admin-catalog>
</div>
</template>
<script>
import AdminCatalog from '../catalog/Catalog'
export default {
	name: 'index',
	components: { AdminCatalog },
	data () {
		return {}
	},
	created () {},
	activated() {
		this.BUS.$emit('setSideActived', this.$route.name)
	},
	mounted () {},
	methods: {},
	watch: {},
	computed: {}
}
</script>
<style scoped lang="less">
.index{}
</style>


