pp，一个最主要的特征就是开放。开放就是共享 App 的流量，让外部伙伴的业务可以通过支付宝触达用户，这就面临一个质量管控的问题。支付宝需要保证这些业务是合法合规的，保障用户的财产安全。")]),_vm._v(" "),_c('p',[_vm._v("在超级 App 时代，使用纯 Native 开发会遇到很大的局限性。这里纯 Native 开发指的是，iOS 使用 Objective-C 或者 Swift 开发、Android 使用 Java 或者 Kotlin开发。纯 Native 开发，业务逻辑的变化都需要通过发布新版本来触达用户，所以会面临前面提到的发布审核周期长的问题。另一方面，用户每次需要使用新功能，都需要更新一次 App ，这对用户来说是一个成本很高的问题，会导致新业务很难在短时间覆盖用户。")]),_vm._v(" "),_c('p',[_vm._v("站在开发的角度，纯 Native 的开发效率会比前端低很多，而且同时需要 iOS 和 Android 的开发人员。包大小也是需要考虑的问题，如果目前支付宝的业务都是用 Native 开发，大家看到的包大小得增加好几倍，这进一步增加了用户的升级成本。作为开放平台，Native 代码的权限和版本管理会变得异常的复杂，每次发版本就都需要同时协调内部和外部伙伴的业务，事情就变得非常糟糕了。")]),_vm._v(" "),_c('p',[_vm._v("所以，现在 App 开发肯定离不开 Hybrid 方案，Hybrid 方案就是混合开发，同时结合了 Native 开发和 Web 开发技术。Web 开发经过 20 年的发展，已经将 结构 HTML、表现 CSS 和 行为 JS 3 部分拆开，因此具有很高的开发效率。Hybrid 方案同时具有跨平台的特征，一份代码同时发布到 iOS 和 Android，节省开发时间。基于 Hybrid 方案开发的业务可以快速发布，而不用考虑 APP 发版实践，给业务很大的灵活性。相比于 Web 开发，Hybrid 方案提供了丰富的设备 API，让更多业务形态成为可能。")]),_vm._v(" "),_c('h3',{attrs:{"id":"articleHeader1"}},[_vm._v("支付宝 Hybrid 方案建设与演进")]),_vm._v(" "),_c('p',[_vm._v("目前支付宝有 2 套 Hybrid 方案： "),_c('strong',[_vm._v("HTML5 容器")]),_vm._v("与"),_c('strong',[_vm._v("小程序")]),_vm._v("。小程序是最近几年才出来，H5 容器已经有了很长时间的历史，所以我们就先从 H5 容器说起。")]),_vm._v(" "),_c('ul',[_c('li',[_c('strong',[_vm._v("H5 容器")])])]),_vm._v(" "),_c('p',[_vm._v("在支付宝中，HTML5 容器架构如图所示：")]),_vm._v(" "),_c('p',[_c('span',{staticClass:"img-wrap"},[_c('img',{staticStyle:{"cursor":"pointer","display":"inline"},attrs:{"referrerpolicy":"no-referrer","data-src":"/img/bVbwBV9?w=1280&h=884","src":"/img/bVbwBV9?w=1280&h=884","alt":"","title":""}})])]),_vm._v(" "),_c('p',[_vm._v("最上层是浏览器，这块就是大家常见的 Web 开发环境，包括 HTML、CSS、JavaScript等。H5 容器作为中间层，将浏览器和支付宝底层框架有机结合起来，在 H5 容器里面有 2 个非常重要的概念： JSBridge 和 离线包，后面会做详细介绍。支付宝底层框架会给 H5 容器提供 Native 的能力，这其中就包括 RPC（远程过程调用，用来实现 APP 和服务器通信）、支付、扫一扫等。")]),_vm._v(" "),_c('ul',[_c('li',[_c('strong',[_vm._v("JSBridge")])])]),_vm._v(" "),_c('p',[_vm._v("JSBridge 是 H5 容器的基石，桥接了 JS 环境与 Native，实现了 Native 代码和 浏览器 环境的双向通信，Native 代码可以通过调用 浏览器 提供的接口运行JS，从而实现调用 JS 函数、传递参数到 JS 环境等；而浏览器到JS