lass:"img-wrap"},[_c('img',{staticStyle:{"cursor":"pointer","display":"inline"},attrs:{"referrerpolicy":"no-referrer","data-src":"/img/bVbwBWg?w=1548&h=776","src":"/img/bVbwBWg?w=1548&h=776","alt":"","title":""}})])]),_vm._v(" "),_c('p',[_vm._v("为了优化 H5 容器用户体验，减少白屏时间，支付宝在 H5 容器中引入了离线包技术。离线包可以简单理解为一个 zip 压缩包，其中包含前端页面所需的 HTML、CSS、JS、图片等资源。内置到客户端后，H5 容器打开离线包页面时会直接从离线包中获取资源，这个是毫秒的访问时间，消除了打开页面白屏现象。")]),_vm._v(" "),_c('p',[_vm._v("在支付宝中，离线包分为 2 种："),_c('strong',[_vm._v("普通的业务资源包")]),_vm._v("和"),_c('strong',[_vm._v("公共资源包")]),_vm._v("。先说公共资源包吧，公共资源包中会包含一些框架 JS、CSS、常见图片等，这些资源在整个 App 里面就只保存一份；而业务资源包中只保存该业务所需的页面静态资源，业务之间是相互独立解耦的。这样一个业务的页面资源就会同时来自其业务资源包和公共资源包，公共资源包的存在优化了 App 大小。")]),_vm._v(" "),_c('p',[_c('span',{staticClass:"img-wrap"},[_c('img',{staticStyle:{"cursor":"pointer","display":"inline"},attrs:{"referrerpolicy":"no-referrer","data-src":"/img/bVbwBWj?w=1630&h=688","src":"/img/bVbwBWj?w=1630&h=688","alt":"","title":""}})])]),_vm._v(" "),_c('p',[_vm._v("为了满足快速发布的需求，H5 容器离线包提供了更新机制，以单个离线包作为更新维度。因为单个离线包业务很简单，所以离线包的大小是可控的，通常小于 500KB，这样单个离线包的更新时间可控，可以做到用户无感知。在一些极端网络场景下，新的业务资源包没有更新超过，而我们又期望用户使用的是最新的业务，这个时候 fallback 访问机制就会发挥作用。每个离线包资源都会在服务器存放一份，在刚刚说到的极端场景下，用户会访问服务器的 fallback 地址获取资源，从而保障页面可用。")]),_vm._v(" "),_c('p',[_c('span',{staticClass:"img-wrap"},[_c('img',{staticStyle:{"cursor":"pointer","display":"inline"},attrs:{"referrerpolicy":"no-referrer","data-src":"/img/bVbwBWk?w=1764&h=932","src":"/img/bVbwBWk?w=1764&h=932","alt":"","title":""}})])]),_vm._v(" "),_c('p',[_vm._v("结合前面说到的离线包后，整个 H5 容器渲染流程如下。其中离线包资源的更新、下载对用户无感知，页面访问的资源是来自离线包还是来自 fallback 地址对前端是无感知的。")]),_vm._v(" "),_c('p',[_c('span',{staticClass:"img-wrap"},[_c('img',{staticStyle:{"cursor":"pointer","display":"inline"},attrs:{"referrerpolicy":"no-referrer","data-src":"/img/bVbwBWp?w=1848&h=1006","src":"/img/bVbwBWp?w=1848&h=1006","alt":"","title":""}})])]),_vm._v(" "),_c('ul',[_c('li',[_c('strong',[_vm._v("UC Webview 内核")])])]),_vm._v(" "),_c('p',[_vm._v("为了提高 H5 容器稳定性，支付宝在安卓系统上使用了 UC Webview，UC Webview 的崩溃率和ANR率远低于