<template>
  <div class="index container">
    <div class="row jumbotron page-header">
      <h1 class="greeting">您好，欢迎访问拉扯网</h1>
      <p>我是威龙</p>
      <p>QQ：894492015</p>
      <p>职业：前端开发(2013年入坑)</p>
      <p>技术栈：HTML/5，CSS/3，Ajax，Jquery，Swiper，Bootstrap，Vue全家桶</p>
    </div>
    <div class="row">
      <div class="col-sm-6 col-md-3" v-for="l in list" :key="l.id">
        <a class="thumbnail" :href="l.url" target="_blank">
          <img :src="l.src" class="icon" :alt="l.title" />
          <div class="caption">
            <h4 v-text="l.title"></h4>
            <p :title="l.desc" v-text="l.desc"></p>
            <span>查看</span>
          </div>
        </a>
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: 'Index',
  data () {
    return {
      list: []
    }
  },
  activated () {
    document.title = "首页";
  },
  mounted () {
    this.getData();
  },
  methods: {
    getData () {
      axios.get('static/db/index.json').then(res => {
        this.list = res.data;
      }).catch(err => {
        console.log(err);
      })
    }
  }
}
</script>
<style scoped>
h1{width:100%;}
.greeting{font-size: 2vw;}
.page-header p{display: block;width:100%;margin-bottom: 5px;}
.thumbnail{width:100%;height:300px;position: relative;overflow: hidden;margin-bottom:20px;display: block;border-radius: 3px;}
.thumbnail .icon{width: 100%;height: auto;display: block;}
.thumbnail .caption{position: absolute;left:0;top:250px;z-index:2;width:100%;height:100%;background-color: rgba(0,0,0,0.7); padding-bottom: 0;transition: all 0.3s;}
.thumbnail:hover .caption{top:0px;}
.caption h4{padding:15px 0;text-align: center;color:#4fc08d;font-size: 1.2vw;}
.caption p{width:100%;color:#4fc08d;font-size: 1vw;padding:0 10px;box-sizing: border-box;word-wrap:break-word;word-break:break-all;}
.caption span{position:absolute;left:0;bottom:0;z-index:2;width:100%;height:40px;line-height: 40px;text-align: center;display:block;color:#fbfa2c;}
@media screen and (min-width: 1100px){
  .index{position: relative;margin-top:30px;}
}
@media screen and (max-width: 1200px){
  .caption h4{font-size: 2vw;}
  .caption p{font-size: 1.5vw;}
}
@media screen and (max-width: 768px){
  .caption h4{font-size: 3vw;}
  .caption p{font-size: 2vw;}
}
@media screen and (max-width: 640px){
  .index{position: relative;top:20px;}
  .thumbnail{height:320px;}
  .thumbnail .caption{top:auto;bottom:0;height:auto;}
  .caption h4{font-size: 4vw;padding-bottom:0;}
  .caption p{font-size: 2.5vw;}
  .caption span{display: none;}
}
</style>


