<template>
<div class="row">
	<p>©<span v-text="nowYear"></span> 
		<a href="http://www.vlon.cn/"> THE VLON.CN COPYRIGHT</a> 
		<a href="http://wpa.qq.com/msgrd?v=3&uin=894492015&site=qq&menu=yes" target="_blank">
			<img border="0" align="absmiddle" title="您好，我是威龙，点击这里可以给我发消息哦~" alt="您好，我是威龙，点击这里可以给我发消息哦~" src="http://wpa.qq.com/pa?p=2:894492015:41" />&nbsp;
		</a> 
	</p>
	<p>
		<a href="http://www.beian.gov.cn/portal/registerSystemInfo?recordcode=11030102010064" target="_blank">
			<img src="http://www.vlon.cn/common/images/beiantubiao.png" alt="京公网安备11030102010064" />京公网安备11030102010064
		</a> 
		<a href="http://www.beian.miit.gov.cn/" target="_blank"> 豫ICP备2026444</a>
		<button class="btn btn-default manage" @click="manage">管理</button>
	</p>
</div>
</template>
<script>
import { Toast } from 'mint-ui'	
import { mapState } from 'vuex'
export default {
	name: 'Copyright',
	data () {
		return {
			nowYear: ''
		}
	},
	computed: {
		...mapState(['server'])
	},
	created () {},
	mounted() {
		this.init()
	},
	methods: {
		init() {
			let nd = new Date()
			this.nowYear = nd.getFullYear()
		},
		manage() {
			let isLogin = JSON.parse(localStorage.lachewang).admin.isLogin
			if(isLogin) {
				this.$router.push('/admin')
			} else {
				this.$router.push('/login')
			}
		}
	}
}
</script>
<style scoped lang="less">
.row{justify-content:center;font-size:0.8vw;color:#666;
	a{color:#666;}
	a:hover{color:#4fc08d;}
	button.manage{font-size:0.5vw; padding:0;margin:0;border:none;font-family: Avenir;line-height: normal;margin-top:-0.2vw;color:#f5f5f5;}
}
</style>


