<template>
<div class="update card">
	<div class="card-header">用户管理</div>
	<ul class="card-body">
		<li>
			<div class="left">账号</div>
			<div class="right"><input type="text" class="form-control account" v-model="account">{{account}}</div>
		</li>
		<li>
			<div class="left">密码</div>
			<div class="right"><input type="password" class="form-control" v-model="password"></div>
		</li>
		<li>
			<div class="left">确认密码</div>
			<div class="right"><input type="password" class="form-control" v-model="repassword"></div>
		</li>
		<li>
			<div class="left">选择角色</div>
			<div class="right">
				<select name="groups" id="groups" class="form-control">
					<option value="超级管理员">超级管理员</option>
					<option value="商品管理员">商品管理员</option>
					<option value="订单管理员">订单管理员</option>
				</select>
			</div>
		</li>
		<li>
			<div class="left">状态</div>
			<div class="right"><mt-switch v-model="switchAccount"></mt-switch></div>
		</li>
	</ul>
	<footer class="card-footer">
		<button class="btn btn-success">Save</button>
	</footer>
</div>
</template>
<script>
import Vue from 'vue'
import { Switch, Toast } from 'mint-ui';
Vue.component(Switch.name, Switch);
export default {
	name: 'Admin_User_Update',
	components: {},
	data () {
		return {
			account: '',
			password: '',
			repassword: '',
			switchAccount: true
		}
	},
	created (){},
	mounted () {},
	methods: {},
	watch: {},
	computed: {}
}
</script>
<style scoped lang="less">
.update{
	ul{width:100%;display: flex;flex-direction: column;
		li{width:100%;display: flex;
			.left{padding:10px 0;display: flex;text-align: right;width:40%;justify-content: flex-end;padding-right: 2%;box-sizing: border-box;}
			.right{padding:10px 0;display: flex;flex:1;}
			.btn{margin:0 auto;flex:1;max-width:700px;min-width: 300px;}
			.account{margin-right: 10px;}
			.form-control{width:30%;min-width: 300px;}
		}
	}
	.card-footer{display: flex;justify-content:center;}
}
</style>


